export default {
	data() {
		return {
			bottomData: [{
					text: '微信',
					icon: '/static/微信.png',
					name: 'wx'
				},
				{
					text: '朋友圈',
					icon: '/static/朋友圈.png',
					name: 'copy'
				},
				{
					text: 'QQ',
					icon: '/static/QQ.png',
					name: 'qq'
				},
				{
					text: '复制',
					icon: '/static/复制链接.png',
					name: 'copy'
				}
			],
			// sharedataTemp: {
			// 	type: 0,
			// 	strShareUrl: "https://uniapp.dcloud.io",
			// 	strShareTitle: "分享标题",
			// 	strShareSummary: "分享总结",
			// 	strShareImageUrl: "/static/sharemenu/copyurl.png"
			// }
		}
	},
	props: ["sharedataTemp"],
	// onLoad() {
	// 	console.log('sharedataTemp',this.sharedataTemp);
	// },
	methods: {
		btnShare(ind) {
			console.log(ind);
			var strProvider = "",
				strScene = "",
				mytype = '';
			switch (ind) {
				case 0:
					strProvider = "weixin"
					strScene = "WXSceneSession"
					mytype = 0
					break;
				case 1:
					strProvider = "weixin"
					strScene = "WXSenceTimeline"
					mytype = 0
					break;
				case 2:
					strProvider = "qq"
					mytype = 1
					break;
				case 3:
					uni.setClipboardData({
						data: this.sharedataTemp.strShareUrl,
						complete() {
							uni.showToast({
								title: "已复制到剪贴板"
							})
						}
					})
					break;
			}
			if (strProvider != "") { //点击了0-3序号的这4个按钮
				uni.share({
					provider: strProvider,
					scene: strScene,
					type: mytype,
					href: this.sharedataTemp.strShareUrl,
					title: this.sharedataTemp.strShareTitle,
					summary: this.sharedataTemp.strShareSummary,
					imageUrl: this.sharedataTemp.strShareImageUrl,
					success: function(res) {
						console.log("success:" + JSON.stringify(res));
					},
					fail: function(err) {
						console.log("fail:" + JSON.stringify(err));
					}
				})
			}
		}
	}
}
